import React, { useEffect } from 'react';
import { connect } from 'react-redux'
import { fetchGroup, removeGroup } from './actions/actions.js'
import { Link } from 'react-router-dom'
import "@fortawesome/fontawesome-free"


import "./style.css"

function App({fetchGroup, getData, removeGroup}) {

  useEffect(()=>{
    fetchGroup()
  }, [])

  

  return (
    <div className = "app">
      <p className = "title"> Список групп </p>
      <ul className="ul">
          {getData.map(i => 
            <li className = "li" key = {i.id}>
              <Link className = "link" to = {{ pathname: `/group/${i.id}`, fromDashboard: false}}>
                  {i.number}
              </Link>
              <button onClick = {()=>removeGroup(i.id)}>Remove</button>
            </li>)
          }
      </ul>
    </div>
  );
}


const mapStateToProps = state => ({
  getData: state.getData
})


const mapDispatchToProps = {fetchGroup, removeGroup}

export default connect(mapStateToProps, mapDispatchToProps)(App);
